/** ===============  Variables =============== **/
body {
  font-family: "Linux Libertine", serif;
}

h1 {
  font: 400 60px/1 "linux";
  color: #00417a;
}

p {
  color: #666;
  font: 400 21px/1.7 "Linux Libertine", serif;
}

.hero-banner {
  background: url(https://jobify-demos.astoundify.com/classic/wp-content/uploads/sites/2/2016/04/hero-image-text-left-darker.jpg);
  height: 70vh;
  width: 100%;
  background-size: cover;
  background-position: 50% 0;
  background-repeat: no-repeat;
}

.page-banner {
  background: url(https://jobify-demos.astoundify.com/classic/wp-content/uploads/sites/2/2016/04/hero-image-text-left-darker.jpg);
  height: 50vh;
  width: 100%;
  background-size: cover;
  background-position: 50% 0;
  background-repeat: no-repeat;
}

.top-navbar {
  font-family: "linux libertine";
  background: #fffefe;
  box-shadow: 0 0 8px 4px #543535;
  padding: 1.3rem;
}
.top-navbar li.nav-item {
  margin: 0 1rem;
}
.top-navbar li.nav-item .nav-link {
  font-size: 1.3rem;
  color: #666;
  font-weight: 300;
  letter-spacing: 1px;
}
.top-navbar li.nav-item:nth-child(2)::after {
  content: "";
  position: relative;
  display: block;
  border-top: 31px solid #e0d8d8;
  width: 1px;
  margin: -38px 0 0 -15px;
}
.top-navbar .loginButton {
  transition: 0.4s;
  border: 1px solid #ececec;
  border-radius: 5px;
  letter-spacing: 3px;
  text-transform: uppercase;
  color: #fffefe !important;
  width: 150px;
  text-align: center;
  padding: 0.4rem;
  background: #00417a;
  font-size: 19px !important;
}
.top-navbar .loginButton:hover {
  background: #821427;
}
.top-navbar img {
  width: 100%;
}

.dark {
  height: 4rem;
  transition: 0.2s;
}
.dark img {
  width: 35%;
  transition: 0.6s;
}

.hero-banner .banner-contant {
  position: absolute;
  top: 37%;
  left: 50%;
  transform: translate(-50%, -43%);
  text-align: center;
  background: rgba(255, 255, 255, 0.7);
  padding: 2rem 10rem;
  text-align: center;
}
.hero-banner .banner-contant h1 {
  font-weight: 300;
  letter-spacing: 6px;
  color: #00417a;
}
.hero-banner .banner-contant p {
  font-family: "linux libertine";
  font-size: 1.3125rem;
  font-weight: 200;
  word-spacing: 5px;
  margin: 1rem 0 3rem 0;
  color: #00417a;
}
.hero-banner .banner-contant a {
  transition: 0.4s;
  display: inline-block;
  background: #00417a;
  color: #fffefe;
  font-size: 12px;
  border-radius: 5px;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 10px 2.2em;
  text-decoration: none;
}
.hero-banner .banner-contant a:hover {
  background: #821427;
  color: #fff;
}
.hero-banner img {
  height: 70vh;
}

.home-section {
  padding: 5rem 0 0 0;
}
.home-section:last-child {
  padding: 5rem;
}
.home-section .section-heading {
  padding: 0 0 2rem 0;
}

.login-hero-banner .login-baner-contant {
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.7);
  padding: 2rem 10rem;
}
.login-hero-banner .login-baner-contant p {
  font-size: 2rem;
}

.login-section {
  width: 37%;
  display: block;
  box-shadow: 0 0 5px -2px black;
  margin: 3rem auto;
  border-radius: 10px;
  padding: 2rem 4rem;
}
.login-section .form-control:focus {
  color: #495057;
  background-color: #fffefe;
  border-color: #80bdff00;
  outline: 1px solid #b5b1b1de;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0);
}
.login-section .email {
  margin: 0 0 2rem 0;
}
.login-section .Action-button {
  transition: 0.4s;
  display: inline-block;
  background: #00417a;
  color: #fffefe;
  font-size: 12px;
  border-radius: 5px;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 10px 2.2em;
}
.login-section .Action-button:hover {
  background: #821427;
  color: #fff;
}
.login-section .form-links {
  margin: 1rem 0 0 0;
}
.login-section .form-links a {
  color: #000;
  font-size: 0.8rem;
}

.form-control:focus {
  color: #495057;
  background-color: #fffefe;
  border-color: #80bdff00;
  outline: 1px solid #b5b1b1de;
  box-shadow: 0 0 0 0.2rem rgba(0, 123, 255, 0);
}

.listing-title-row p {
  font-weight: 100;
  text-align: center;
  margin: 3rem 0 2rem 0;
  color: #2b2b2b;
}

.searching-filters-row {
  background: #eee;
  padding: 2rem 1rem 1rem;
  border-radius: 10px;
  box-shadow: 0 0 4px -1px #00000082;
}

.Action-button {
  transition: 0.4s;
  display: inline-block;
  background: #00417a;
  color: #fffefe;
  font-size: 12px;
  border-radius: 5px;
  letter-spacing: 2px;
  text-transform: uppercase;
  padding: 10px 2.2em;
}
.Action-button:hover {
  background: #821427;
  color: #fff;
}

.jobcard .card {
  margin: 1rem 0;
  border-radius: 10px;
  box-shadow: 0 0 4px -1px #00000082;
}
.jobcard .JD {
  display: flex;
}
.jobcard .JD .experties {
  flex-grow: 1;
}

.details .appy-button {
  float: right;
  margin: 1rem 2rem;
}

.footer {
  font-family: "linux libertine";
  display: flex;
  flex-flow: row wrap;
  color: #fffefe;
}
.footer .main-footer {
  background-color: #00417a;
  padding: 3rem;
}
.footer .main-footer .footer__nav {
  display: flex;
  flex-flow: row wrap;
}
.footer .main-footer .footer__nav .nav__item {
  font-weight: 100;
}
.footer .main-footer .footer__nav .nav__item h4 {
  font-size: 1rem;
}
.footer .main-footer address {
  font-style: normal;
  color: #fffefe;
}
.footer .main-footer ul {
  list-style: none;
}
.footer .main-footer ul li {
  text-align: center;
}
.footer .main-footer ul li a {
  color: #fffefe;
  text-decoration: none;
}
.footer .copyright {
  background: #2b2b2b;
}
.footer .copyright p {
  color: #fffefe;
  padding: 1rem 3rem 0 3rem;
}
.footer .copyright .social-media {
  position: absolute;
  right: 0;
  padding: 1rem 3rem;
}
.footer .copyright .social-media span {
  margin: 0 1rem;
}
.footer .copyright .social-media span a {
  color: #fffefe;
}

.footer > * {
  flex: 1 100%;
}

.footer__nav > * {
  flex: 1 50%;
}

@media screen and (min-width: 24.375em) {
  .legal .legal__links {
    margin-left: auto;
  }
}
@media screen and (min-width: 40.375em) {
  .footer__nav > * {
    flex: 1;
  }

  .nav__item--extra {
    flex-grow: 2;
  }

  .footer__addr {
    flex: 0 0;
  }

  .footer__nav {
    flex: 2 0px;
  }
}